<template>
    <div class=" mt72  bx">

      <div class="nav">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/homeCommon/classManagement' }">班级管理</el-breadcrumb-item>
          <el-breadcrumb-item>创建班级</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="createClassa  bf bx">
      <p class="tit ft20">
        创建班级
      </p>
      <div class="con">
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px"
                 class="demo-ruleForm">



          <el-form-item label="班级名称" prop="name" class="pas_icon inp">
            <el-input v-model.number="ruleForm2.name" placeholder="请输入班级名称" ></el-input>
          </el-form-item>

          <el-form-item label="班级属性" prop="gradeIds" class="sec">
            <el-select v-model="ruleForm2.gradeIds" placeholder="请选择年级"    @change="currentSel" value-key="gradeId">
              <el-option
                v-for="items in createClass"
                :key="items.gradeId"
                :label="items.gradeName"
                :value="items">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="subjectId" class="sec">
            <el-select v-model="ruleForm2.subjectId" placeholder="请选择科目">
              <el-option
                v-for="(item,i) in andSubjectList"
                :key="item.id"
                :label="item.subjectName"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')"  >确定</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    </div>
</template>

<script>
    export default {
      name: "createClass",
      data() {
        return {
          checked: true,
          createClass:[],
          andSubjectList:[],
          ruleForm2: {
            name:'',
            subjectId: '',
            gradeIds:''
          },
          rules2: {
            name: [
              { required: true, message: '请输入班级名称',  trigger: 'blur' },
              { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
            ],
            gradeIds: [
              { required: true, message: '请选择年级', trigger: 'change' }
            ],
            subjectId: [
              { required: true, message: '请选择科目', trigger: 'change' }
            ],
          },
          gradeId:"",
          activeIndex:""
        };
      },
      created() {
        this.queryGradeAndSubjectList()//获取年级学科下拉列表
      },
      methods: {
        //选择班级
        currentSel(selVal) {
          this.gradeId = selVal.gradeId;
          this.andSubjectList = selVal.list
          console.log(selVal,"55");
        },
        //获取年级学科下拉列表
        queryGradeAndSubjectList(){
          this.$post('/likework/queryGradeAndSubjectList.do')
            .then((response) => {
              //console.log(response)
              if (response.code == "5000") {
                this.createClass = response.data
              }
            })
        },
        //创建
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              const loadingObj = this.$loading({
                lock: true,
                text: '提交中...',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)',
                target: document.querySelector('.submit-test-dialog')
              });
              this.$post('/likework/createClass.do',{
                subjectId:this.ruleForm2.subjectId,
                gradeId:this.gradeId,
                name:this.ruleForm2.name
              })
                .then((response) => {
                  //console.log(response)
                  if (response.code == "5000") {
                    loadingObj.close()
                    this.$message({
                      showClose: true,
                      message: '创建班级成功',
                      type: 'success'
                    });
                    setTimeout(()=>{
                      this.$router.push({
                        path:'/homeCommon/classManagement/invitationCode'
                        , query: {id: response.data}
                      })
                    },1000);
                  }else{
                    loadingObj.close()
                    this.$message({
                      showClose: true,
                      message: response.message,
                      type: 'error'
                    });
                  }
                })

            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }
      },
    }
</script>

<style scoped lang="less">
  .createClassa{
    padding: 40px 30px 100px 30px ;
    margin: 0 auto 200px auto;
    .tit{
      position: relative;
      padding-left: 18px;
      &::before{
        position: absolute;
        content: '';
        display: inline-block;
        left: 0;
        border-left: 4px solid #ff8a00;
        height: 25px;
        border-radius: 5px;
        top: 2px;
      }
    }
    .con{
      margin: 50px 0 0 120px;
      .inp{

      }
    }
  }
  .nav{
    margin: 20px 0;
  }
</style>
<style>
  .createClassa .con .inp .el-input,.createClassa .con .sec .el-select{width: 40%;}
</style>



